/*eslint-disable*/
import React, { PureComponent } from 'react';
import Types from 'prop-types';
import { formatWithPattern, unformatWithPattern, getCursorPosition } from '../common/textFormat';

class InputText extends PureComponent {
  static propTypes = {
    pattern: Types.string,
    placeholder: Types.string,
  };

  static defaultProps = {
    pattern: '',
    placeholder: '',
  };

  constructor(props) {
    super(props);
    this.state = {
      value: '',
      prevPattern: props.pattern,
    };
    this.input = React.createRef();
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (prevState.prevPattern !== nextProps.pattern) {
      const { value, prevPattern } = prevState;
      const { pattern } = nextProps;
      const unFormattedValue = unformatWithPattern(value, prevPattern);
      const formattedValue = formatWithPattern(unFormattedValue, pattern);
      return {
        prevPattern: pattern,
        value: formattedValue,
      };
    }
    return null;
  }

  setCursor = select => {
    setTimeout(() => {
      this.input.current.selectionStart = select;
      this.input.current.selectionEnd = select;
    }, 0);
  };

  handleOnChange = e => {
    e.preventDefault();
    const { value } = e.target;
    const { pattern } = this.props;

    let formattedValue = value;
    let unFormattedValue = value;
    let cursorPosition = this.input.current.selectionStart;

    if (pattern) {
      unFormattedValue = unformatWithPattern(value, pattern);
      formattedValue = formatWithPattern(unFormattedValue, pattern);
      cursorPosition = getCursorPosition(this.input.current.selectionStart, pattern);
    }

    this.setState({ value: formattedValue }, this.setCursor(cursorPosition));
  };

  render() {
    const { value } = this.state;
    const { placeholder } = this.props;

    return (
      <input
        type="text"
        value={value}
        ref={this.input}
        className="form-control"
        onChange={e => this.handleOnChange(e)}
        placeholder={placeholder}
      />
    );
  }
}

export default InputText;



