import getPatternSymbolsWitPosition from '../getPatternSymbolsWitPosition';

const getCursorPosition = (currentCursorPosition, pattern) => {
  if (!currentCursorPosition && currentCursorPosition !== 0) {
    return 0;
  }
  if (!pattern) {
    return currentCursorPosition;
  }

  const patternWithSymbolsPosition = getPatternSymbolsWitPosition(pattern);
  let newCursorPosition = currentCursorPosition;

  let i = 0;
  if (patternWithSymbolsPosition.length) {
    while (i < patternWithSymbolsPosition.length) {
      if (patternWithSymbolsPosition[i].index + 1 === newCursorPosition) {
        newCursorPosition += 1;
      }
      i += 1;
    }
  }

  return newCursorPosition;
};

export default getCursorPosition;



